.container-left-row{
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #F5F5F5;
}

.summary-container-main{
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 10rem;
  padding: 1rem 0;
}

.summary-container-left{
  width: 30%;
}

.summary-container-left-solo{
  width: 45%;
}

.summary-header-row{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: end;
      align-items: flex-end;
  border-bottom: 1px solid #E0E0E0;
  height: 35px;
  padding-bottom: 5px;
}

.summary-container-right{
  width: 65%;
}

.row-right{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.row-right > * {
  display: inline-block;
  width: 33.33%;
}

.summary-container-footer{
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.label{
  margin:0;
  color: #757575;
}

.value {
  margin: 0;
  color: #6ac1bf;
}

.brand {
  font-size: 20px;
  font-weight: bold;
}

.status {
  color: #FFA000;
  font-weight: bold;
}

.arrow-head {
  padding: 0 1.5rem;
}

.status-pagination{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  padding-bottom: .5rem 0;
}

.history {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
}



